* {
  box-sizing: border-box;
}
.box {
  overflow: hidden;
}
.participation {
  float: left;
  width: 50%;
  padding-left: 50px;
  padding-bottom: 50px;
  list-style: none;
  border-right: 1px solid #e4e4e4;
}
.participation > li {
  margin: 7px;
  display: inline-block;
  width: 40%;
}
.participation > li > button {
  margin-left: 7px;
}
.participation > h2 {
  text-align: center;
  padding-bottom: 30px;
}
.unparticipation {
  float: left;
  width: 50%;
  padding-left: 50px;
  padding-bottom: 50px;
  list-style: none;
}
.unparticipation > li {
  margin: 7px;
  display: inline-block;
  width: 40%;
}
.unparticipation > li > button {
  margin-left: 7px;
}
.unparticipation > h2 {
  text-align: center;
  padding-bottom: 30px;
}
.result {
  display: inline-block;
  margin-bottom: 30px;
  margin-right: 10px;
  margin-left: 30px;
  font-size: larger;
}
.result1 {
  display: inline-block;
  font-size: larger;
}

.result2 {
  margin-left: 10px;
  margin-right: 20px;
  display: inline-block;
  font-size: larger;
}
.result > h2 {
  text-align: left;
}
.buttonBox {
  display: block;
  margin: 0 auto;
}
.buttonBox button {
  margin: 20px;
  width: 30%;
  font-size: 2rem;
}
